.bar {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 550px;
    height: 350px;
    border: 3px solid gray;
    border-radius: 10px;
    background-color: #fff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -75%);
    z-index: 2;

    p {
        text-decoration: underline;
        font-weight: 800;
    }

    img {
        width: 35px;
        height: 35px;
        position: absolute;
        right: 10px;
        top: 10px;
        cursor: pointer;
    }

    h1 {
        margin-top: 30px;
    }

    form {
        width: 400px;
        height: 200px;
        margin-top: 30px;
        border: none;
        display: flex;
        flex-direction: column;
        align-items: flex-end;

        textarea {
            font-size: 20px;
            margin-top: 7.5px;
            width: 100%;
        }

        input {
            width: 100%;

            &[type="submit"] {
                width: 20%;
                margin-right: 0;
                margin-top: 7.5px;
            }
        }
    }
}